:root {
  /*text size*/
  /*color gray*/
}
*, *:after, *:before {
  box-sizing: border-box;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  direction: ltr;
  font: 300 14px/1.8 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
html, body {
  width: 100%;
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html, body, ul, ol, h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
}
article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
  display: block;
}
button {
  background-color: transparent;
  outline: none;
  font: 300 14px/1.8 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
a {
  text-decoration: none;
  transition: color ease .3s;
  color: #A779E8;
  cursor: pointer
}
a:hover {
  color: #3071a9;
}
::-moz-selection {
  color: #222;
  background: #d6edff;
  text-shadow: none;
}
::selection {
  color: #222;
  background: #d6edff;
  text-shadow: none;
}
/*浏览器默认样式重置*/
ul {
  list-style: none;
}
/*左侧菜单栏*/
.el-menu {
  background-color: #EFF3F9;
}
.el-submenu .el-menu {
  background-color: #FDFFFF;
}
.el-submenu .el-menu-item:hover {
  background-color: #EEF4FF;
}
.el-submenu .el-menu-item.is-active::before {
  content: '';
  display: block;
  position: absolute;
  top: 17px;
  left: 56px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 8px;
  border-color: transparent transparent transparent #39B0F7;
}
.el-submenu__title {
  color: #333333;
  font-weight: 400;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both
}
.fl-l {
  float:left;
}
.fl-r {
  float:right;
}
.centa {text-align: center}
.lefta {text-align: left}
.righta {text-align: right}
.flex-s {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.flex-y {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.list-unstyle {
  list-style: none;
}
.list-inline>li {
  display: inline-block;
}
.pull-right {
  float: right;
}
.pull-left {
  float: left;
}
.position-right {
  position: absolute;
  right: 0;
}
.mr-normal {
  margin-right: 1em;
}
.position-relative {
  position: relative;
}
/*fade 过渡*/
.fade-enter-active, .fade-leave-active {
  transition: opacity .7s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.animation-delay-half {
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}
.animation-delay-1 {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.animation-delay-2 {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.animation-delay-3 {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.animation-delay-4 {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
.animation-delay-5 {
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
}
.fast-animated {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}
/*list-complete*/
.list-complete-item {
  transition: all 1s;
  display: inline-block;
  margin-right: 10px;
}
.list-complete-enter, .list-complete-leave-active {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}
.list-complete-leave-active {
  position: absolute;
}



